@import "./mixins.scss";
// Gilroy-bold
@include font-face("Gilroy", "../fonts/Gilroy-Bold.eot", 700, normal, "eot");
@include font-face("Gilroy", "../fonts/Gilroy-Bold.ttf", 700, normal, "ttf");
@include font-face("Gilroy", "../fonts/Gilroy-Bold.woff", 700, normal, "woff");
@include font-face("Gilroy", "../fonts/Gilroy-Bold.woff2", 700, normal, "woff2");

// Inter-bold

@font-face {
    font-family: 'Inter';
    src: url('../fonts/Inter18pt-Bold.woff2') format('woff2'),
        url('../fonts/Inter18pt-Bold.woff') format('woff');
    font-weight: 700;
    font-style: normal;
    font-display: swap;
}

