/* _____ Basic elements _____ */

@mixin font-face($font-family, $url, $weight, $style, $format) {
  @font-face {
    font-family: #{$font-family};
    src: url('../fonts/#{$url}') format("#{$format}");
    font-weight: #{$weight};
    font-display: swap;
    font-style: #{$style};
  }
}

@mixin placeholder {
  &::-webkit-input-placeholder {
    @content;
  }

  &:-moz-placeholder {
    @content;
  }

  &::-moz-placeholder {
    @content;
  }

  &:-ms-input-placeholder {
    @content;
  }
}

// ==========================================================================================================
// flex-box mixins
@mixin flex-btw-center {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

@mixin flex-column-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@mixin flex-row-center {
  display: flex;
  flex-direction: row;
  align-items: center;
}

