@mixin fz($fontsize, $fzxl: xl, $fzlg: lg, $fzmd: md, $fzsm: sm, $fzxs: xs, $prop: 'font-size') {
  #{$prop}: $fontsize;

  $sizes: (xl: calc_responsive_font_size($fontsize, xl),
      lg: calc_responsive_font_size($fontsize, lg),
      md: calc_responsive_font_size($fontsize, md),
      sm: calc_responsive_font_size($fontsize, sm),
      xs: calc_responsive_font_size($fontsize, xs));

  @if $fzxl==xl {
    @include media-breakpoint-down(xl) {
      #{$prop}: map-get($sizes, $fzxl);
    }
  }

  @else if $fzxl !=null {
    @include media-breakpoint-down(xl) {
      $size-for-fzxl: map-get($sizes, $fzxl);

      @if $size-for-fzxl {
        #{$prop}: $size-for-fzxl;
      }

      @else {
        #{$prop}: $fzxl;
      }
    }
  }

  @if $fzlg==lg {
    @include media-breakpoint-down(lg) {
      #{$prop}: map-get($sizes, $fzlg);
    }
  }

  @else if $fzlg !=null {
    @include media-breakpoint-down(lg) {
      $size-for-fzlg: map-get($sizes, $fzlg);

      @if $size-for-fzlg {
        #{$prop}: $size-for-fzlg;
      }

      @else {
        #{$prop}: $fzlg;
      }
    }
  }

  @if $fzmd==md {
    @include media-breakpoint-down(md) {
      #{$prop}: map-get($sizes, $fzmd);
    }
  }

  @else if $fzmd !=null {
    @include media-breakpoint-down(md) {
      $size-for-fzmd: map-get($sizes, $fzmd);

      @if $size-for-fzmd {
        #{$prop}: $size-for-fzmd;
      }

      @else {
        #{$prop}: $fzmd;
      }
    }
  }

  @if $fzsm==sm {
    @include media-breakpoint-down(sm) {
      #{$prop}: map-get($sizes, $fzsm);
    }
  }

  @else if $fzsm !=null {
    @include media-breakpoint-down(sm) {
      $size-for-fzsm: map-get($sizes, $fzsm);

      @if $size-for-fzsm {
        #{$prop}: $size-for-fzsm;
      }

      @else {
        #{$prop}: $fzsm;
      }
    }
  }

  @if $fzxs==xs {
    @include media-breakpoint-down(xs) {
      #{$prop}: map-get($sizes, $fzxs);
    }
  }

  @else if $fzxs !=null {
    @include media-breakpoint-down(xs) {
      $size-for-fzxs: map-get($sizes, $fzxs);

      @if $size-for-fzxs {
        #{$prop}: $size-for-fzxs;
      }

      @else {
        #{$prop}: $fzxs;
      }
    }
  }

}


